<template>
    <div>
        <div class="title-container">
            <h3 class="title">Links</h3>
            <button type="button" class="btn-create" data-bs-toggle="modal" data-bs-target="#linkModal" data-toggle="tooltip" title="Create link" @click="resetVariables" v-if="profile === 'Admin'"></button>
        </div>
        
        <div>
            <div class="link-container" v-for="link in links" :key="link.id">
                <div class="link-data-container" @click="openNewTab(link.data.url)">
                    <h5 class="link-title">{{ link.data.name }}</h5>
                    <p class="link-description">{{ link.data.description }}</p>
                </div>
                <div class="link-admin-options" v-if="profile === 'Admin'">
                    <button class="btn-admin-options btn-edit" data-bs-toggle="modal" data-bs-target="#updateLinkModal" data-toggle="tooltip" title="Edit link" @click="openUpdateModal(link)"></button>
                    <button class="btn-admin-options btn-delete" data-bs-toggle="modal" data-bs-target="#deleteLinkModal" data-toggle="tooltip" title="Delete link" @click="openDeleteModal(link)"></button>
                </div>
            </div>
        </div>

        <!-- Modals -->
        <!-- Create modal -->
        <div class="modal fade" id="linkModal" data-bs-backdrop="static" data-bs-keyboard="false" tabindex="-1" aria-labelledby="linkModalLabel" aria-hidden="true">
            <div class="modal-dialog">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title" id="linkModalLabel">CREATE NEW LINK</h5>
                        <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                    </div>
                    <div class="modal-body mt-2">
                        <div class="input-container">
                            <label for="link-name">Name:</label>
                            <input type="text" id="link-name" v-model="name">
                        </div>
                        <div class="input-container">
                            <label for="link-description">Description:</label>
                            <textarea type="area" id="link-description" v-model="description"></textarea>
                        </div>
                        <div class="input-container">
                            <label for="link-link">Link (url):</label>
                            <input type="text" id="link-link" v-model="url">
                        </div>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn-submit-modal" :disabled="allCampsFilled" data-bs-dismiss="modal" aria-label="Close" @click="createLink">Create</button>
                    </div>
                </div>
            </div>
        </div>
        
        <!-- Update modal -->
        <div class="modal fade" id="updateLinkModal" data-bs-backdrop="static" data-bs-keyboard="false" tabindex="-1" aria-labelledby="updateLinkModalLabel" aria-hidden="true">
            <div class="modal-dialog" v-if="selectedLink === ''">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title" id="deleteLinkModalLabel">UPDATE LINK</h5>
                        <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                    </div>
                    <div class="modal-body">
                        <div>
                            There is some problem with this action. Refresh and try again or contact with the developer.
                        </div>
                    </div>
                </div>
            </div>
            <div class="modal-dialog" v-else>
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title" id="updateLinkModalLabel">UPDATE LINK</h5>
                        <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                    </div>
                    <div class="modal-body mt-2">
                        <div class="input-container">
                            <label for="link-name">Name:</label>
                            <input type="text" id="link-name" v-model="name">
                        </div>
                        <div class="input-container">
                            <label for="link-description">Description:</label>
                            <textarea type="area" id="link-description" v-model="description"></textarea>
                        </div>
                        <div class="input-container">
                            <label for="link-link">Link (url):</label>
                            <input type="text" id="link-link" v-model="url">
                        </div>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn-submit-modal" :disabled="updatedCampsUnchanged || allCampsFilled" data-bs-dismiss="modal" aria-label="Close" @click="updateLink(selectedLink.id)">Update</button>
                    </div>
                </div>
            </div>
        </div>

        <!-- Delete modal -->
        <div class="modal fade" id="deleteLinkModal" data-bs-backdrop="static" data-bs-keyboard="false" tabindex="-1" aria-labelledby="deleteLinkModalLabel" aria-hidden="true">
            <div class="modal-dialog" v-if="selectedLink === ''">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title" id="deleteLinkModalLabel">DELETE LINK</h5>
                        <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                    </div>
                    <div class="modal-body">
                        <div>
                            There is some problem with this action. Refresh and try again or contact with the developer.
                        </div>
                    </div>
                </div>
            </div>
            <div class="modal-dialog" v-else>
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title" id="deleteLinkModalLabel">DELETE LINK</h5>
                        <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                    </div>
                    <div class="modal-body">
                        <div>
                            Are you sure you want to delete the link with name <strong>{{ selectedLink.data.name }}</strong>?
                        </div>
                    </div>
                    <div class="modal-footer modal-delete-btns-container">
                        <button type="button" class="btn-submit-modal modal-delete-btn red-background" data-bs-dismiss="modal" aria-label="Close" @click="removeLink(selectedLink.id)">Delete</button>
                        <button type="button" class="btn-submit-modal modal-delete-btn" data-bs-dismiss="modal" aria-label="Close">Do NOT delete</button>
                    </div>
                </div>
            </div>
        </div>
        
    </div>
</template>

<script>
import { db } from "@/firebase/firebase.js";
import { collection, doc, getDocs, addDoc, updateDoc, deleteDoc } from "firebase/firestore";
import { mapGetters } from "vuex";

export default {
    name: 'Links',
    data() {
        return {
            links: [],
            name: "",
            description: "",
            url: "",
            selectedLink: "",
        }
    },
    computed: {
        ...mapGetters(["profile"]),
        allCampsFilled() {
            return !(this.name !== "" && this.description !== "" && this.url !== "");
        },
        updatedCampsUnchanged() {
            return (this.name === this.selectedLink.data.name && this.description === this.selectedLink.data.description && this.url === this.selectedLink.data.url);
        },
    },
    methods: {
        async getLinks() {
            this.links = [];
            try {
                const querySnapshot = await getDocs(collection(db, "links"));
                querySnapshot.forEach((link) => {
                    if(link.data().visible) this.links.push({id: link.id, data: link.data()});
                });
            } catch(e) {
                console.log(e.message);
            }
        },
        async createLink() {
            try {
                const docRef = await addDoc(collection(db, "links"), {
                    name: this.name,
                    description: this.description,
                    url: this.url,
                    visible: true
                });
                console.log(docRef);
            } catch(e) {
                console.log(e);
            }
            this.resetVariables();
            this.getLinks();
        },
        async updateLink(linkId) {
            const docToUpdate = doc(db, "links", linkId);
            await updateDoc(docToUpdate, {
                name: this.name,
                description: this.description,
                url: this.url,
            });
            this.getLinks();
        },
        async removeLink(linkId) {
            await deleteDoc(doc(db, "links", linkId));
            this.getLinks();
        },
        openUpdateModal(link) {
            this.selectedLink = link;
            this.name = link.data.name;
            this.description = link.data.description;
            this.url = link.data.url
        },
        openDeleteModal(link) {
            this.selectedLink = link;
        },
        openNewTab(url) {
            window.open(url, "_blank");
        },
        resetVariables() {
            this.name = "";
            this.description = "";
            this.url = "";
        }
    },
    mounted() {
        this.getLinks();
    },
}
</script>

<style scoped>
.btn-create {
    background-image: url("@/assets/create.png");
    background-repeat: no-repeat;
    background-position: center;
    background-color: rgba(64, 224, 208, 0.25);

    width: 35px;
    height: 35px;
    border-radius: 5px;
    border: 1px solid black;
    margin-top: 3px;
    transition: 0.5s;
}

.btn-create:hover {
    background-color: rgba(64, 224, 208, 0.75);
}

.link-container {
    display: flex;
    gap: 5px;
    margin-bottom: 5px;
}

.link-data-container {
    width: 100%;
    border: 1px solid black;
    border-radius: 15px;
    padding: 2rem;
    cursor: pointer;
    transition: 0.5s;
}

.link-container:nth-child(even) > .link-data-container {
    background: rgba(194, 194, 194, 0.25);
}

.link-container:nth-child(even)  > .link-data-container:hover {
    background: rgba(194, 194, 194, 0.75);
}

.link-container:nth-child(odd) > .link-data-container {
    background: rgba(253, 197, 0, 0.25);
}

.link-container:nth-child(odd) > .link-data-container:hover {
    background: rgba(253, 197, 0, 0.75);
}

.link-title {
    font-weight: bold;
}

.link-description {
    margin: 0;
}

.link-admin-options {
    margin: 1rem 0;
    display: flex;
    flex-direction: column;
    justify-content: start;
    gap: 1rem;
}

.btn-admin-options {
    width: 35px;
    height: 35px;
    border-radius: 5px;
    border: 1px solid black;
    background-repeat: no-repeat;
    background-position: center;
    transition: 0.5s;
}

.btn-edit {
    background-image: url("@/assets/edit.png");
    background-color: rgba(255, 166, 0, 0.5);
}

.btn-edit:hover {
    background-image: url("@/assets/edit.png");
    background-color: rgba(255, 166, 0, 1);
}

.btn-delete {
    background-image: url("@/assets/delete.png");
    background-color: rgba(255, 0, 0, 0.5);
}

.btn-delete:hover {
    background-color: rgba(255, 0, 0, 1);
}

.input-container {
    display: flex;
    flex-direction: column;
    margin-bottom: 1rem;
}

.btn-submit-modal {
    border: none;
    background: rgba(73,73,73,0.75);
    color: white;
    border-radius: 5px;
    padding: 8px 15px;
    font-size: 18px;
    transition: 0.5s;
}

.btn-submit-modal:hover {
    background: rgba(73,73,73,1);
}

.btn-submit-modal:disabled {
    background: rgba(73,73,73,0.25);
}

.red-background {
    background-color: rgb(205, 0, 0) !important;
}

.red-background:hover {
    background-color: rgb(235, 0, 0) !important;
}

.modal-delete-btn {
    width: 175px;
}

.modal-delete-btns-container {
    justify-content: space-between;
}
</style>