import { createStore } from 'vuex'

export default createStore({
  state: {
    // REMEMBER TO CHANGE THIS LINE WHEN DEBUG OR PRODUCTION
    // debugMode: "true",
    debugMode: "false",

    login: "false",
    uid: "",
    profile: "",
  },
  getters: {
    debugMode(state) {
      return state.debugMode === "true";
    },
    isLogin(state) {
      return state.login === "true";
    },
    uid(state) {
      return state.uid;
    },
    profile(state) {
      return state.profile;
    }
  },
  mutations: {
    setLogin(state, login) {
      state.login = login ? "true" : "false";
    },
    setUid(state, uid) {
      state.uid = uid;
    },
    setProfile(state, profile) {
      state.profile = profile;
    }
  },
  actions: {
  },
  modules: {
  }
})
