<template>
<div class="header-container">
  <div class="logo-container">
    <img src="@/assets/sabena-white.png" alt="">
  </div>
  <div class="login-logout-icon-container" :class="[profile === '' ? '' : 'logout-top-position']">
    <router-link class="btn-login-logout" to="/" v-if="profile === ''">
      <span class="btn-login-logout-icon btn-login-icon-white"></span>
      <span class="btn-login-logout-icon btn-login-icon-yellow"></span>
      <span class="btn-login-text">Login</span>
    </router-link>
    <router-link class="btn-login-logout" to="/" @click="logout" v-else>
      <span class="btn-login-logout-icon btn-logout-icon-white"></span>
      <span class="btn-login-logout-icon btn-logout-icon-yellow"></span>
      <span class="btn-logout-text">Logout</span>
    </router-link>
  </div>
  <nav class="navbar-container">
    <router-link :class="[currentPath === 'home' ? 'navbar-element-active' : '']" class="navbar-element first-navbar-element" to="/">
      Home
    </router-link>
    <router-link :class="[currentPath === 'about' ? 'navbar-element-active' : '']" class="navbar-element" to="/about">
      About
    </router-link>
    <router-link :class="[currentPath === 'links' ? 'navbar-element-active' : '']" class="navbar-element" to="/links" v-if="['Admin', 'Basic'].includes(profile)">
      Links
    </router-link >
    <router-link :class="[currentPath === 'admin' ? 'navbar-element-active' : '']" class="navbar-element" to="/admin" v-if="profile === 'Admin'">
      Admin
    </router-link>
  </nav>
</div>
<router-view class="main-container" />
<footer class="footer-container">
  <p>
    Copyright © 2023 Sabena Technics BGC
  </p>
</footer>
</template>

<script>
import { auth, db } from "@/firebase/firebase.js";
import { onAuthStateChanged, signOut } from "firebase/auth";
import { doc, getDoc } from "firebase/firestore";
import { mapGetters, mapMutations } from "vuex";

export default {
  computed: {
    ...mapGetters(["profile"]),
    currentPath() {
      return this.$route.name;
    }
  },
  methods: {
    ...mapMutations(["setLogin", "setUid", "setProfile"]),
    logout() {
      signOut(auth).then(() => {
        console.log("Sign-out successful");
      }).catch((error) => {
        console.log("An error happened: "+error.code);
      });
    },
  },
  mounted() {
    onAuthStateChanged(auth, (user) => {
      if(user) {
        const uid = user.uid;
        // console.log(uid);
        this.setLogin(true);
        this.setUid(uid);
        const docRef = doc(db, "users", uid);
        getDoc(docRef)
          .then(snapShot => {
            this.setProfile(snapShot.data().profile);
          })
      }else {
        // console.log("no-login");
        this.setLogin(false);
        this.setUid("");
        this.setProfile("");
      }
    });
  },
}
</script>

