<template>
    <div>
        <form class="login-logout-container" onsubmit="return false">
            <div class="login-container" v-if="!isLogin">
                <div class="input-container">
                    <label for="email">Email:</label>
                    <input type="text" name="" id="email" v-model="email">
                </div>
                <div class="input-container">
                    <label for="password">Password:</label>
                    <input type="password" name="" id="password" v-model="password">
                </div>
                <div class="error-container">
                    <p>
                        {{ loginErrorMessage }}
                    </p>
                </div>
                <div class="submit-container">
                    <input type="submit" class="btn-submit" :disabled="!allInputsFilled" @click="login" value="Login">
                </div>
            </div>
            <div class="logout-container" v-else>
                <div class="logout-text">
                    You are already logged-in. If you want to use another account, first log-out.
                </div>
                <div class="btn-logout-container">
                    <button class="btn-submit" @click="logout">
                        Logout
                    </button>
                </div>
            </div>
        </form>
        
    </div>
</template>

<script>
import { auth } from "@/firebase/firebase.js";
import { signInWithEmailAndPassword, signOut } from "firebase/auth";
import { mapGetters } from "vuex";

export default {
    name: 'Login',
    data() {
        return {
            email: "",
            password: "",
            loginErrorMessage: "",
        }
    },
    computed: {
        ...mapGetters(["isLogin"]),
        allInputsFilled() {
            return this.email !== "" && this.password !== "";
        }
    },
    methods: {
        login() {
            this.loginErrorMessage = "";
            signInWithEmailAndPassword(auth, this.email, this.password)
                .then((userCredential) => {
                    // Signed in 
                    const user = userCredential.user;
                    // console.log(user);
                    console.log("Sign-in successful");
                    this.$router.push({name: "links"})
                })
                .catch((error) => {
                    const errorCode = error.code;
                    if(errorCode === "auth/invalid-email") {
                        this.loginErrorMessage = "Invalid email";
                    }
                    else if(errorCode === "auth/user-not-found") {
                        this.loginErrorMessage = "Email doesn't exist";
                    }
                    else if(errorCode === "auth/missing-password") {
                        this.loginErrorMessage = "Password is empty";
                    }
                    else if(errorCode === "auth/wrong-password") {
                        this.loginErrorMessage = "Wrong password";
                    }
                    else if(errorCode === "auth/too-many-requests") {
                        this.loginErrorMessage = "You reached the maximum attempts, wait a few minutes";
                    }
                    // console.log(error.code)
                });
            // this.email = "";
            // this.password = "";
        },
        logout() {
            signOut(auth).then(() => {
                    console.log("Sign-out successful");
                }).catch((error) => {
                    console.log("An error happened: "+error.code);
                });
        },
    },
    watch: {
        isLogin() {
            this.email = "";
            this.password = "";
        }
    }    
}
</script>

<style scoped>
.login-logout-container {
    margin-top: 2rem;
    border: 1px solid rgba(0, 0, 0, 0.5);
    border-radius: 15px;
    padding: 25px;
    width: 500px;
}

.input-container {
    display: flex;
    flex-direction: column;
}

.input-container:nth-child(1) {
    margin-bottom: 2rem;
}

.error-container {
    /* border: 1px solid red; */
    margin: 1rem;
    color: rgb(205, 0, 0);
    height: 18px;
    font-size: 14px;
    text-align: center;
}

.error-container > p {
    margin: 0;
}

.submit-container {
    display: flex;
    justify-content: end;
}

.btn-submit {
    border: none;
    background: rgba(73,73,73,0.75);
    color: white;
    border-radius: 5px;
    padding: 5px 25px;
    font-size: 18px;
    transition: 0.5s;
}

.btn-submit:hover {
    background: rgba(73,73,73,1);
}

.btn-submit:disabled {
    background: rgba(73,73,73,0.25);
}

.logout-text {
    opacity: 0.75;
    font-style: oblique;
}

.btn-logout-container {
    margin-top: 2rem;
    display: flex;
    justify-content: center;
}

.btn-logout-container > button {
    width: 250px;
}
</style>