<template>
    <div>
        <div class="title-container">
            <h3 class="title">Users</h3>
            <button type="button" class="btn-create" data-bs-toggle="modal" data-bs-target="#createUserModal" data-toggle="tooltip" title="Create user" @click="resetVariables"></button>
        </div>
        
        <div>
            <div class="user-container" v-for="user in users" :key="user.id">
                <div class="user-data-container">
                    <h5 class="user-full-name">{{ user.data.fullName }}</h5>
                    <p class="user-email">Email: <strong>{{ user.data.email }}</strong></p>
                    <p class="user-email">Profile: <strong>{{ user.data.profile }}</strong></p>
                </div>
                <div class="user-options">
                    <button class="btn-options btn-edit" data-bs-toggle="modal" data-bs-target="#updateUserModal" data-toggle="tooltip" title="Edit user" @click="openUpdateModal(user)"></button>
                    <button class="btn-options btn-delete" data-bs-toggle="modal" data-bs-target="#deleteUserModal" data-toggle="tooltip" title="Delete user" @click="openDeleteModal(user)"></button>
                </div>
            </div>
        </div>
        
        <!-- Modals -->
        <!-- Create modal -->
        <div class="modal fade" id="createUserModal" data-bs-backdrop="static" data-bs-keyboard="false" tabindex="-1" aria-labelledby="createUserModalLabel" aria-hidden="true">
            <div class="modal-dialog">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title" id="createUserModalLabel">CREATE NEW USER</h5>
                        <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                    </div>
                    <div class="modal-body mt-2">
                        <div class="input-container">
                            <label for="user-full-name">Full name:</label>
                            <input type="text" id="user-full-name" v-model="fullName">
                        </div>
                        <div class="input-container">
                            <label for="user-email">Email:</label>
                            <input type="email" id="user-email" v-model="email">
                        </div>
                        <div class="input-container">
                            <span>Profile:</span>
                            <div class="radio-container">
                                <div class="radio-element-container">
                                    <input type="radio" id="user-profile-basic" name="create-profile" value="Basic" v-model="profile">
                                    <label for="user-profile-basic">Basic</label>
                                </div>
                                <div class="radio-element-container">
                                    <input type="radio" id="user-profile-admin" name="create-profile" value="Admin" v-model="profile">
                                    <label for="user-profile-admin">Admin</label>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn-submit-modal" :disabled="allCampsFilled" data-bs-dismiss="modal" aria-label="Close" @click="createUser">Create</button>
                    </div>
                </div>
            </div>
        </div>

        <!-- Update modal -->
        <div class="modal fade" id="updateUserModal" data-bs-backdrop="static" data-bs-keyboard="false" tabindex="-1" aria-labelledby="updateUserModalLabel" aria-hidden="true">
            <div class="modal-dialog" v-if="selectedUser === ''">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title" id="deleteUserModalLabel">UPDATE USER</h5>
                        <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                    </div>
                    <div class="modal-body">
                        <div>
                            There is some problem with this action. Refresh and try again or contact with the developer.
                        </div>
                    </div>
                </div>
            </div>
            <div class="modal-dialog" v-else>
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title" id="updateUserModalLabel">UPDATE USER</h5>
                        <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                    </div>
                    <div class="modal-body mt-2">
                        <div class="input-container">
                            <label for="user-full-name">Full name:</label>
                            <input type="text" id="user-full-name" v-model="fullName">
                        </div>
                        <div class="input-container">
                            <span>Profile:</span>
                            <div class="radio-container">
                                <div class="radio-element-container">
                                    <input type="radio" id="user-profile-basic" name="update-profile" value="Basic" v-model="profile">
                                    <label for="user-profile-basic">Basic</label>
                                </div>
                                <div class="radio-element-container">
                                    <input type="radio" id="user-profile-admin" name="update-profile" value="Admin" v-model="profile">
                                    <label for="user-profile-admin">Admin</label>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn-submit-modal" :disabled="updatedCampsUnchanged || allUpdateableCampsFilled" data-bs-dismiss="modal" aria-label="Close" @click="updateUser(selectedUser.id)">Update</button>
                    </div>
                </div>
            </div>
        </div>

        <!-- Delete modal -->
        <div class="modal fade" id="deleteUserModal" data-bs-backdrop="static" data-bs-keyboard="false" tabindex="-1" aria-labelledby="deleteUserModalLabel" aria-hidden="true">
            <div class="modal-dialog" v-if="selectedUser === ''">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title" id="deleteUserModalLabel">DELETE USER</h5>
                        <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                    </div>
                    <div class="modal-body">
                        <div>
                            There is some problem with this action. Refresh and try again or contact with the developer.
                        </div>
                    </div>
                </div>
            </div>
            <div class="modal-dialog" v-else>
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title" id="deleteUserModalLabel">DELETE USER</h5>
                        <button type="button" class="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                    </div>
                    <div class="modal-body">
                        <div>
                            Are you sure you want to delete the user profile of <strong>{{ selectedUser.data.fullName }}</strong>?
                        </div>
                    </div>
                    <div class="modal-footer modal-delete-btns-container">
                        <button type="button" class="btn-submit-modal modal-delete-btn red-background" data-bs-dismiss="modal" aria-label="Close" @click="removeUser(selectedUser.id)">Delete</button>
                        <button type="button" class="btn-submit-modal modal-delete-btn" data-bs-dismiss="modal" aria-label="Close">Do NOT delete</button>
                    </div>
                </div>
            </div>
        </div>
        

    </div>
</template>

<script>
import { auth, db, authToCreateUsers } from "@/firebase/firebase.js";
import { collection, doc, query, where, getDocs, setDoc, updateDoc } from "firebase/firestore";
import { createUserWithEmailAndPassword, sendPasswordResetEmail } from "firebase/auth";


export default {
    name: 'Admin',
    data() {
        return {
            users: [],
            fullName: "",
            email: "",
            profile: "Basic",
            selectedUser: "",
        }
    },
    computed: {
        allCampsFilled() {
            return !(this.fullName !== "" && this.email !== "" && this.profile !== "");
        },
        allUpdateableCampsFilled() {
            return !(this.fullName !== "" && this.profile !== "");
        },
        updatedCampsUnchanged() {
            return (this.fullName === this.selectedUser.data.fullName && this.profile === this.selectedUser.data.profile);
        }
    },
    methods: {
        async getUsers() {
            this.users = [];
            try {
                const querySnapshot = await getDocs(collection(db, "users"));
                querySnapshot.forEach((doc) => {
                    // doc.data() is never undefined for query doc snapshots
                    if(doc.data().active) this.users.push({id: doc.id, data: doc.data()});
                });
            } catch(e) {
                console.log(e.message);
            }
        },
        async createUser() {
            const uid = await createUserWithEmailAndPassword(authToCreateUsers, this.email, this.generateRandomPassword(10))
                .then(user => {
                    const uid = user.user.uid;
                    authToCreateUsers.signOut();
                    return uid;
                })
                .catch(e => {
                    if(e.code === "auth/email-already-in-use") {
                        return "Reactivate account";
                    } else {
                        console.log(e.message);
                    }
                });
            if(uid === "Reactivate account") {
                const q = query(collection(db, "users"), where("email", "==", this.email));
                const querySnapshot = await getDocs(q);
                querySnapshot.forEach(async (document) => {
                    try {
                        const docToUpdate = doc(db, "users", document.id);
                        const email = this.email;
                        const result = await updateDoc(docToUpdate, {
                            fullName: this.fullName,
                            profile: this.profile,
                            active: true
                        })
                            .then(() => {
                                return "success";
                            })
                            .catch((e) => {
                                console.log(e);
                            });
                        if(result === "success") {
                            sendPasswordResetEmail(auth, email)
                                .then(() => {})
                                .catch((e) => {
                                    console.log(e);
                                });
                        }
                    } catch(e) {
                        console.log(e.message);
                    }
                });
            } else {
                const result = await setDoc(doc(db, "users", uid), {
                    fullName: this.fullName,
                    email: this.email,
                    profile: this.profile,
                    active: true
                })
                    .then(() => {
                        return "success";
                    })
                    .catch((e) => {
                        console.log(e);
                    });
                if(result === "success") {
                    sendPasswordResetEmail(auth, this.email)
                        .then(() => {})
                        .catch((e) => {
                            console.log(e);
                        });
                }
                
            }
                
            this.resetVariables();
            this.getUsers();
        },
        async updateUser(userId) {
            const docToUpdate = doc(db, "users", userId);
            await updateDoc(docToUpdate, {
                fullName: this.fullName,
                profile: this.profile,
            });
            this.getUsers();
        },
        async removeUser(userId) {
            const docToUpdate = doc(db, "users", userId);
            await updateDoc(docToUpdate, {
                active: false,
                profile: "Basic",
            });
            this.getUsers();
        },
        openUpdateModal(user) {
            this.selectedUser = user;
            this.fullName = user.data.fullName;
            this.profile = user.data.profile;
        },
        openDeleteModal(user) {
            this.selectedUser = user;
        },
        resetVariables() {
            this.fullName = "";
            this.email = "";
            this.profile = "Basic";
        },
        generateRandomPassword(n){
            const chars = [];
            const letters = "0123456789ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz";
            while(chars.length < n) {
                chars.push(letters.charAt(Math.random()*letters.length));
            }
            return chars.join("");
        }
    },
    mounted() {
        this.getUsers();
    },
}
</script>

<style scoped>
.btn-create {
    background-image: url("@/assets/create.png");
    background-repeat: no-repeat;
    background-position: center;
    background-color: rgba(64, 224, 208, 0.25);

    width: 35px;
    height: 35px;
    border-radius: 5px;
    border: 1px solid black;
    margin-top: 3px;
    transition: 0.5s;
}

.btn-create:hover {
    background-color: rgba(64, 224, 208, 0.75);
}

.user-container {
    display: flex;
    gap: 5px;
    margin-bottom: 5px;
}

.user-data-container {
    width: 100%;
    border: 1px solid black;
    border-radius: 15px;
    padding: 2rem;
    transition: 0.5s;
}

.user-container:nth-child(even) > .user-data-container {
    background: rgba(194, 194, 194, 0.25);
}

.user-container:nth-child(even)  > .user-data-container:hover {
    background: rgba(194, 194, 194, 0.75);
}

.user-container:nth-child(odd) > .user-data-container {
    background: rgba(253, 197, 0, 0.25);
}

.user-container:nth-child(odd) > .user-data-container:hover {
    background: rgba(253, 197, 0, 0.75);
}

.user-full-name {
    font-weight: bold;
}

.user-email {
    margin: 0;
}

.user-options {
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 1rem;
}

.btn-options {
    width: 35px;
    height: 35px;
    border-radius: 5px;
    border: 1px solid black;
    background-repeat: no-repeat;
    background-position: center;
    transition: 0.5s;
}

.btn-edit {
    background-image: url("@/assets/edit.png");
    background-color: rgba(255, 166, 0, 0.5);
}

.btn-edit:hover {
    background-image: url("@/assets/edit.png");
    background-color: rgba(255, 166, 0, 1);
}

.btn-delete {
    background-image: url("@/assets/delete.png");
    background-color: rgba(255, 0, 0, 0.5);
}

.btn-delete:hover {
    background-color: rgba(255, 0, 0, 1);
}

.input-container {
    display: flex;
    flex-direction: column;
    margin-bottom: 1rem;
}

.radio-container {
    padding: 10px;
    border: 1px solid rgba(0, 0, 0, 0.4);
    border-radius: 2.5px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    gap: 15px;
}

.radio-container:hover {
    border: 1px solid rgba(0, 0, 0, 0.6);
}

.radio-element-container > input, label{
    cursor: pointer;
}

.radio-element-container > label{
    padding-left: 3px;
}

.btn-submit-modal {
    border: none;
    background: rgba(73,73,73,0.75);
    color: white;
    border-radius: 5px;
    padding: 8px 15px;
    font-size: 18px;
    transition: 0.5s;
}

.btn-submit-modal:hover {
    background: rgba(73,73,73,1);
}

.btn-submit-modal:disabled {
    background: rgba(73,73,73,0.25);
}

.red-background {
    background-color: rgb(205, 0, 0) !important;
}

.red-background:hover {
    background-color: rgb(235, 0, 0) !important;
}

.modal-delete-btn {
    width: 175px;
}

.modal-delete-btns-container {
    justify-content: space-between;
}
</style>